import * as React from 'react';
import { useState } from 'react';
import { pageBack, hovering } from './backButton.module.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faChevronUp} from '@fortawesome/free-solid-svg-icons';
import AniLink from "gatsby-plugin-transition-link/AniLink"

const BackButton = () => {

    const [isHover, setIsHover] = useState(false);
    function handleToggle(){
        setIsHover(!isHover);
    }

    return(
        <div>
            <AniLink swipe direction="down" top="exit" to="/" entryOffset={100} duration={1}>
                <FontAwesomeIcon icon={faChevronUp} className={`${pageBack} ${isHover ? "fa-beat" : undefined} ${isHover ? hovering : undefined}`} style={{color: "#e3eff5",}} onMouseOver={handleToggle} onMouseOut={handleToggle} />
            </AniLink>
        </div>
    )
}

export default BackButton